@media (min-width: 320px) and (max-width: 426px) {
	.container {
		width: 375px; }
	.navbar {
		height: 64px;
		&_title {
			display: none; } }
	.img-cornerUp, .img-cornerDown {
		display: none; }
	.logo {
		margin: 0 auto;
		padding-left: 0px;
		padding-top: 17px;
		img {
			width: 37px;
			height: 29px; } }
	.main {
		display: block;
		height: 958px; }
	.avatar {
		padding-left: 0px;
		padding-top: 0px;
		img {
			width: 348px;
			height: 396px;
			margin-top: 24px;
			margin-left: 14px; } }
	.header {
		width: 100%;
		padding-left: 0px;
		h1 {
			padding-top: 0px;
			font-size: 40px;
			line-height: 48px;
			margin-top: 24px;
			margin-left: 14px; }
		&_text {
			padding-top: 16px;
			margin: 0px;
			font-size: 15px;
			line-height: 24px;
			width: 348px;
			margin-left: 14px; }
		&_about {
			padding-top: 32px;
			margin: 0px;
			font-size: 20px;
			line-height: 24px;
			margin-left: 14px; }
		&_title {
			padding-top: 16px;
			margin-left: 14px;
			div {
				display: block;
				margin-right: 39px;
				span {
					font-size: 14px;
					line-height: 18px; }
				p {
					margin-left: 0px;
					margin-top: 4px;
					font-size: 14px;
					line-height: 18px; } } } }
	.img-squarePurpleSmall {
		left: 321px;
		top: 335px;
		width: 32px;
		height: 32px;
		transform: rotate(45deg);
		img {
			width: 32px;
			height: 32px;
			border-radius: 50%; } }
	.img-squarePinkMiddle {
		left: 332px;
		top: 366px;
		width: 0px;
		height: 0px;
		transform: rotate(64deg);
