			width: 60px;
			height: 60px;
			transform: rotate(45deg);
			border-radius: 100%; } }
	.img-squareGrayMiddle, .img-squareGraySmall, .img-squareGrayBig, .img-squareDarkGray, .img-squarePurpleMiddle, .img-squarePinkBig {
		display: none; }
	.img-mobil1 {
		position: absolute;
		display: inline-block;
		right: 70px;
		top: 90px;
		img {
			width: 32px;
			height: 32px;
			transform: rotate(73deg);
			border-radius: 50%; } }
	.img-mobil2 {
		position: absolute;
		display: inline-block;
		right: 20px;
		top: 35px;
		img {
			width: 60px;
			height: 60px;
			transform: rotate(45deg);
			border-radius: 100%; } }
	.addDocument {
		width: 348px;
		height: 175px;
		left: 14px;
		top: 653px;
		display: block;
		p {
			width: 212px;
			text-align: center;
			margin-left: 68px;
			margin-top: 25px; }
		button {
			margin-left: 68px;
			margin-top: 15px; }
		input {
			&:last-child {
				margin-top: -15px;
				margin-left: 0px; } } }
	.addDocument-span {
		left: 14px;
		bottom: 145px;
		width: 93%; }
	.content {
		height: 1037px;
		.title {
			font-size: 32px;
			line-height: 41px;
			padding-left: 0px;
			padding-top: 40px;
			padding-right: 0px;
			width: 348px;
			margin-left: 14px; }
		.fields {
			left: 14px;
			top: 100px;
			padding-top: 20px;
			padding-left: 0px;
			display: block;
			div {
				width: 102%;
				margin-right: 0px; }
			select {
				width: 323px; }
			&_input {
				width: 102%;
				font-size: 16px;
				line-height: 24px; }
			p {
				margin-top: 40px; } }
		&_button {
			left: 14px;
			width: 93%; } }
	#input3 {
		display: block;
		left: 0px;
		top: 270px; }
	#input4 {
		display: none !important; }
	#input5 {
		display: block;
		left: 0px;
		top: 390px; }
	body {
		width: 375px; }
	.completed {
		&_flex {
