	.footer {
		height: 595px; }
	.slider {
		left: 14px;
		top: 32px;
		&_item {
			img {
				width: 346px;
				height: 535px; } }
		&_prev, &_next {
			top: 86%; } }
	.sliderText {
		width: 225px;
		left: 60px;
		top: 160px;
		font-size: 40px;
		line-height: 48px;
		text-align: center; }
	.sliderTextDescr {
		width: 254px;
		left: 45px;
		top: 275px;
		font-size: 15px;
		line-height: 24px; }
	.slider-dots {
		left: 37%;
		bottom: 45px; }
	.slider_item_imgDesktop {
		display: none; }
	.slider_item_imgMobile {
		display: block; }
	.addDocument-flex {
		div {
			&:first-child {
				width: 14%; }
			&:nth-child(2) {
				width: 76%; }
			&:nth-child(3) {
				width: 4%;
				margin-bottom: 7px; } } } }
